<template>
  <div class="app-layout">
    <!-- 侧边菜单 -->
    <aside class="sidebar" :class="{ 'collapsed': isCollapse }">
      <div class="logo">
        <img src="@/assets/logo.png" alt="Logo" class="logo-img" />
        <span v-if="!isCollapse">闲鱼商品发布助手</span>
      </div>
      
      <div class="menu-container">
        <div 
          class="menu-item" 
          :class="{ 'active': activeMenu === '/dashboard' }"
          @click="navigateTo('/dashboard')"
        >
          <el-icon><Odometer /></el-icon>
          <span v-show="!isCollapse">仪表盘</span>
        </div>
        
        <div 
          class="menu-item" 
          :class="{ 'active': activeMenu === '/shops' }"
          @click="navigateTo('/shops')"
        >
          <el-icon><Shop /></el-icon>
          <span v-show="!isCollapse">店铺管理</span>
        </div>
        
        <div 
          class="menu-item" 
          :class="{ 'active': activeMenu === '/images' }"
          @click="navigateTo('/images')"
        >
          <el-icon><Picture /></el-icon>
          <span v-show="!isCollapse">图片管理</span>
        </div>
        
        <div 
          class="menu-item" 
          :class="{ 'active': activeMenu === '/keywords' }"
          @click="navigateTo('/keywords')"
        >
          <el-icon><Collection /></el-icon>
          <span v-show="!isCollapse">关键词管理</span>
        </div>
        
        <div 
          class="menu-item" 
          :class="{ 'active': activeMenu === '/product-publish' }"
          @click="navigateTo('/product-publish')"
        >
          <el-icon><Upload /></el-icon>
          <span v-show="!isCollapse">商品发布</span>
        </div>
        
        <div 
          class="menu-item" 
          :class="{ 'active': activeMenu === '/publish-tasks' }"
          @click="navigateTo('/publish-tasks')"
        >
          <el-icon><List /></el-icon>
          <span v-show="!isCollapse">发布任务池</span>
        </div>
        
        <div 
          class="menu-item" 
          :class="{ 'active': activeMenu === '/coze-test' }"
          @click="navigateTo('/coze-test')"
        >
          <el-icon><Connection /></el-icon>
          <span v-show="!isCollapse">Coze工作流测试</span>
        </div>
        
        <div 
          class="menu-item" 
          :class="{ 'active': activeMenu === '/settings' }"
          @click="navigateTo('/settings')"
        >
          <el-icon><Setting /></el-icon>
          <span v-show="!isCollapse">设置</span>
        </div>
      </div>
      
      <div class="collapse-control" @click="isCollapse = !isCollapse">
        <el-icon>
          <Expand v-if="isCollapse" />
          <Fold v-else />
        </el-icon>
      </div>
    </aside>
    
    <!-- 主内容区 -->
    <div class="main-container">
      <!-- 顶部导航栏 -->
      <header class="header">
        <div class="page-title">{{ currentPageTitle }}</div>
        
        <div class="right">
          <el-dropdown trigger="click">
            <div class="user-info">
              <span>{{ username }}</span>
              <el-icon><ArrowDown /></el-icon>
            </div>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item @click="goToSettings">个人设置</el-dropdown-item>
                <el-dropdown-item divided @click="logout">退出登录</el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </div>
      </header>
      
      <!-- 内容区 -->
      <main class="main">
        <div class="content-wrapper">
          <router-view v-slot="{ Component }">
            <transition name="fade" mode="out-in">
              <component :is="Component" />
            </transition>
          </router-view>
        </div>
      </main>
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useUserStore } from '../stores/user'
import { 
  Odometer, 
  Shop, 
  Picture, 
  Setting, 
  Expand, 
  Fold, 
  ArrowDown,
  Connection,
  Upload,
  Promotion,
  List,
  Collection
} from '@element-plus/icons-vue'

export default {
  name: 'LayoutPage',
  components: {
    Odometer,
    Shop,
    Picture,
    Setting,
    Expand,
    Fold,
    ArrowDown,
    Connection,
    Upload,
    Promotion,
    List,
    Collection
  },
  setup() {
    const route = useRoute()
    const router = useRouter()
    const userStore = useUserStore()
    
    // 侧边栏折叠状态
    const isCollapse = ref(false)
    
    // 当前激活的菜单
    const activeMenu = computed(() => {
      const { path } = route
      return path
    })
    
    // 当前页面标题
    const currentPageTitle = computed(() => {
      const { meta } = route
      return meta.title || '首页'
    })
    
    // 用户名
    const username = computed(() => userStore.username)
    
    // 导航到指定路由
    const navigateTo = (path) => {
      router.push(path)
    }
    
    // 跳转到设置页
    const goToSettings = () => {
      router.push('/settings')
    }
    
    // 退出登录
    const logout = () => {
      userStore.logout()
    }
    
    return {
      isCollapse,
      activeMenu,
      currentPageTitle,
      username,
      navigateTo,
      goToSettings,
      logout
    }
  }
}
</script>

